// prefixer helper function
var prefixes = ['webkit', 'moz', 'MS', 'o', ''];

var add_event_listener = function add_event_listener(element, type, callback) {
  prefixes.forEach(function (prefix) {
    if (!prefix) {
      type = type.toLowerCase();
    }

    element.addEventListener(prefix + type, callback, false);
  });
};

module.exports = function (element) {
  return {
    animate: function animate(animation, opts) {
      opts = opts || {};
      element.className = element.className.replace(/\b[a-z0-9\-]+\-animation/gi, '').trim();
      var class_name = animation + '-animation';
      element.classList.add('animated');

      if (opts.infinite) {
        element.classList.add('infinite');
      }

      element.classList.add(class_name);

      if (opts.keep) {
        return this;
      }

      this.end(function (e) {
        element.classList.remove(class_name);
      });
      return this;
    },
    start: function start(cb) {
      add_event_listener(element, 'AnimationStart', cb);
      return this;
    },
    end: function end(cb) {
      add_event_listener(element, 'AnimationEnd', cb);
      return this;
    },
    iteraction: function iteraction(cb) {
      add_event_listener(element, 'AnimationIteraction', cb);
      return this;
    }
  };
};